import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue';
import uploadImages from '@/components/common/upload-images.vue';
export default {
	components:{
		uniNavBar,
		uploadImages
	},
	data() {
		return {
			content:'',
			imageList:[],
			showBack:false
		}
	},
	onBackPress() {
		console.log(11);
		if((this.content !== '' || this.imageList.length>0 ) && !this.showBack){
			uni.showModal({
				content: '是否要保存草稿',
				showCancel: true,
				cancelText: '不保存',
				confirmText: '保存',
				success: res => {
					if(res.confirm){
						console.log('保存')
					}
					uni.navigateBack({delta: 1});
					
				}
			});
			this.showBack = true;
			return true
		}
	},
	methods: {
		changeImages(e){
			this.imageList = e;
			console.log(e);
		}
		
	}
}
